class list extends Sv.Component {
    constructor(pops) {
        super(pops);
        this.state = { ll: '333' }
    }

    componentWillMount() {/* 在渲染前调用 */
        console.log('2');
    }
    componentDidMount() { /* 在第一次渲染后调用 */
    }
    componentDidUpdate() {/* 在组件完成更新后立即调用。在初始化时不会被调用 */
    }
    componentWillUnmount() {/* 在组件从 DOM 中移除之前被调用 */

    }
    methods = {
        event: function (e) {
            console.log('event');
        }
    }
}
